//react 基础组件加载
import * as React from "react";
import { Button, Tabs } from 'antd-mobile';
import qs from 'qs'

//页面样式加载
import DetailTitle from '../components/detailTitle';
import CardPanl from '../components/CardPanl'
import FootAdvertising from '../components/advertising/footAdvertising'
import CourseSeat from '../components/course/CourseSeat'
import DownLoad from '../components/down/downLoad'
import '../assets/less/coursedetail.less';

import { getCourseDetail } from '../api/course'

interface ISprops {
    [propName: string]: any;
}
interface IState {
    tabs: { title: string }[],
    currentTab: number,
    courseDetail: any,
    showSign: boolean,
    source: boolean,
    type: boolean,
    downShow: boolean
}
class CourseDetail extends React.Component<ISprops, IState> {
    constructor(props: any) {
        super(props);
        this.state = {
            tabs: [
                {
                    title: '课程详情'
                },
                {
                    title: '我要报名'
                }
            ],
            currentTab: 0,
            courseDetail: {},
            showSign: true,
            source: true,//设置来源 组建的个性化配置
            type: false,//消费详情进入为true
            downShow: false
        }
    }
    changeTabs = (tab: any, index: any) => {
        this.setState({
            currentTab: index,
            showSign: (index === 0 ? true : false)
        })
    }
    handleChangeSign = () => {
        this.setState({
            currentTab: 1
        })
    }
    getCourseInfo = () => {//页面数据
        let search = this.props.location.search;
        let data = qs.parse(search.substring(1));

        let { id } = this.props.match.params;
        this.setState({
            type: data.type || false
        })
        getCourseDetail({
            id,
        }).then(res => {
            this.setState({
                courseDetail: res.data || []
            })
        })
    }
    componentDidMount() {
        document.title = '课程详情';
        this.getCourseInfo()
    }
    downLoad = () => {
        //下载大纲
        this.setState({
            downShow: true
        })
    }
    closeDownLoad = () => {
        this.setState({
            downShow: false
        })
    }
    render() {
        const { tabs, currentTab, courseDetail, showSign, source, type, downShow } = this.state;
        console.log(courseDetail)
        return (
            <div className="course-detail" style={{ position: 'relative' }}>
                <div className="detail-banner">
                    <img src={courseDetail.img_url} alt="" />
                </div>
                
                        <Tabs tabs={tabs}
                            page={currentTab}
                            onChange={this.changeTabs}
                        >
                            <div className="tabs-detail" style={{paddingBottom:'1rem'}}>
                                <DetailTitle course={courseDetail} source={source} />
                                <CardPanl title="课程介绍">
                                    <p>{courseDetail.course_content}</p>
                                </CardPanl>
                                {
                                    (courseDetail.course_file_name && courseDetail.course_file_url) ? 
                                        <CardPanl title="课程大纲">
                                            <p>{courseDetail.course_file_name}</p>
                                            <div className="detail-c-b clearfix">
                                                <a className="left"  target="_blank" type="primary"  rel="noopener noreferrer" href={`${courseDetail.course_file_url}`}>在线查看</a>
                                                <Button className="left" type="primary" size="small" onClick={this.downLoad}>下载大纲</Button>
                                            </div>
                                        </CardPanl>
                                        :
                                        <div></div>
                                }
                                <FootAdvertising  title="course-detail"/>

                            </div>
                            <div className="detail-app">
                                {
                                    (courseDetail.course_list || []).map((el: any, index: number) => {
                                        return (
                                            <CourseSeat key={index} courseSeat={el} />
                                        )
                                    })
                                }
                                <FootAdvertising title="course-detail"/>
                            </div>
                        </Tabs>

                {
                    showSign && currentTab === 0 && !type ?
                        <div className="sign-up">
                            <Button type="primary" className="sign-up-btn" onClick={this.handleChangeSign}>前往报名</Button>
                        </div>
                        :
                        <div></div>
                }
                <DownLoad title="大纲" downShow={downShow} detail={courseDetail} closeDownLoad={this.closeDownLoad} downLoad={this.downLoad}/>
            </div>
        );
    }

}

export default CourseDetail