//react 基础组件加载
import * as React from "react";
import { Button, Badge } from 'antd-mobile'
import {getSeatActiveTime} from '../../util'
import { getUserInfo } from '../../api/bind'
import {withRouter} from "react-router";

interface ISprops {
    [propName: string]: any;
}
interface IState {
    [propName: string]: any;
}

class CourseSeat extends React.Component<ISprops, IState> {
    constructor(props: any) {
        super(props);
        this.state = {
            user_type: null
        }
    }
    handleSumbit = () => {
        getUserInfo({}).then( res => {
            if (!res.status) {
                this.setState({user_type: res.data.user_type},() => {
                    if(this.state.user_type === 2) {
                        this.props.history.push(`/applyOnline?memberType=0&courseId=${this.props.courseSeat.id}`)
                    } else {
                        this.props.history.push(`/normalMember?courseId=${this.props.courseSeat.id}`)
                    }
                })
            } 
        })
    }
    render() {
        const {courseSeat} = this.props
        const activeTime = getSeatActiveTime(courseSeat.start_time || '' ,courseSeat.end_time || '')
        console.log(activeTime)
        return (
            <div className="detail-app-i clearfix">
                <div className="detail-app-i-l left">
                    <h3>
                        {courseSeat.province == '' ? courseSeat.city : `${courseSeat.province}-${courseSeat.city}`}
                        {/* <Badge text="名"></Badge> */}
                    </h3>
                    <p>{activeTime}</p>
                </div>
                <div className="detail-app-i-m left">
                    <p className={ courseSeat.surplus_seat < 20 && courseSeat.surplus_seat !== 0 ? "site-little" : ""}>剩余席位：{courseSeat.surplus_seat}</p>
                    <p>{`计费${courseSeat.amount}元/`} {!courseSeat.course_count ? '不可计次' : `计次${courseSeat.course_count}次`}</p>
                </div>
                <div className="detail-app-i-r left">
                    <Button type="primary" className="btn" disabled={courseSeat.surplus_seat ===0 ? true : false} size="small" onClick={this.handleSumbit}>报名</Button>
                </div>
            </div>
        );
    }

}

//@ts-ignore
export default withRouter(CourseSeat);